import { Component, OnInit } from '@angular/core';
import { FormBuilder, FormGroup, Validators } from '@angular/forms';
import { Router } from '@angular/router';
import { data } from 'jquery';
import Swal from 'sweetalert2';
import { ServiceService } from '../services/service.service';
declare var tinymce:any;
declare var iziToast:any;
@Component({
  selector: 'app-video-upload',
  templateUrl: './video-upload.component.html',
  styleUrls: ['./video-upload.component.css']
})
export class VideoUploadComponent implements OnInit {
videoform:FormGroup;
categorylists;
catselect;
subcatselect;
subcategorylists;
posts;
  status: any;
  constructor(private fb: FormBuilder,private serverService: ServiceService, private router:Router) { 
    this.videoform = this.fb.group({
      post_title: ['', Validators.required],
      status: ['',Validators.required],
      public: ['',Validators.required],
      private: ['',Validators.required],
      Both: ['',Validators.required],
      video_link: ['',Validators.required],
      ck_editor: ['',Validators.required]
      });
    // this.editor = this.fb.group({
    //   ck_editor: ['',Validators.required]
    // })  
  }

  ngOnInit(): void {
    this.getcategory();
    this.initTiny();
    
  }
  
  // editor(){
  //   console.log(this.ck_editor)
  // }
  getcategory(){
    Swal.close();
    let api_req:any = '{"operation": "category","moduleType": "category","api_type": "web","element_data": {"action":"selectcategory"}}';
    this.serverService.sendserver(api_req).subscribe((response: any)=>{
      console.log(response)
      if(response.status == true){
        // console.log("asdf")
        this.categorylists = response.result.data;
      }
  },(error: any)=>{
      console.log(error)
  });
  console.log();
}
getsubcategory(){
  let api_req:any = '{"operation": "category","moduleType": "category","api_type": "web","element_data": {"action":"select_sub_category","category_id":"'+this.catselect+'"}}';
  this.serverService.sendserver(api_req).subscribe((response: any)=>{
    console.log(response)
    if(response.status == true){
      // console.log("asdf")
      this.subcategorylists = response.result.data;
    }
},(error: any)=>{
    console.log(error)
});
console.log();
}
  postdata(videoform){
  //  console.log(btoa(tinymce.activeEditor.getContent()));
  //  let post = btoa(tinymce.activeEditor.getContent());
  //  console.log(post)
  //   return false;
    var post_title = videoform.value.post_title;
    var status = videoform.value.status;
    // let check : any;
    if(status == true){
      status = 1;
    }else{
      status = 0;
    }
    var display_type = videoform.value.private;
    var post_by = $('#opt').val();
    var video_link = videoform.value.video_link;
    var ck_editor = btoa(tinymce.activeEditor.getContent());
    let api_req:any = '{"operation": "category","moduleType": "category","api_type": "web","element_data": {"action":"post","post_title":"'+post_title+'","video_link":"'+video_link+'","status":"'+status+'","category_id":"'+this.catselect+'","subcat_id":"'+this.subcatselect+'","post_content":"'+ck_editor+'","display_type":"'+display_type+'","post_by":"'+post_by+'"}}';
    Swal.fire({
      html:
        '<div style="display: flex;justify-content: center;"><div class="pong-loader"></div></div>',
    showCloseButton: false,
      showCancelButton: false,
      showConfirmButton: false,
      focusConfirm: false,
      background: 'transparent',
  
  
    });
    this.serverService.sendserver(api_req).subscribe((response: any)=>{
    //   console.log(api_req)
    // return false;
        Swal.close();
        if(response.result.data == 1){
          this.posts = response.result.data;
          iziToast.success({
            message: "Video link added",
            position: 'topRight'
        });
          this.router.navigate(['/kb/displaypage'])
        }
    },(error: any)=>{
        console.log(error)
    });
    console.log(videoform);
  }
  cancel(){
    this.videoform.reset();
    $('.form-control').val('');
    $('#ck_editor').val('');
    tinymce.activeEditor.setContent('');
  }
  initTiny(){
    
    tinymce.init({
      selector : 'textarea',
      plugins : 'advlist autolink lists link  image charmap print preview anchor searchreplace visualblocks code fullscreen insertdatetime media table paste help wordcount autolink lists media table',
      toolbar : 'undo redo| fullscreen | formatselect | fontselect | fontsizeselect| bold italic | \ undo redo | link image file| code | \
      alignleft aligncenter alignright alignjustify | \
      bullist numlist outdent indent | help',
  
      content_style: 'body {font-size: 10pt;font-family: Verdana;}',
      images_upload_url : 'upload.php',
      automatic_uploads : false,
  
      images_upload_handler : function(blobInfo, success, failure) {
        var xhr, formData;
  
        xhr = new XMLHttpRequest();
        xhr.withCredentials = false;
        xhr.open('POST', 'upload.php');
  
        xhr.onload = function() {
          var json;
  
          if (xhr.status != 200) {
            failure('HTTP Error: ' + xhr.status);
            return;
          }
  
          json = JSON.parse(xhr.responseText);
  
          if (!json || typeof json.file_path != 'string') {
            failure('Invalid JSON: ' + xhr.responseText);
            return;
          }
  
          success(json.file_path);
        };
  
        formData = new FormData();
        formData.append('file', blobInfo.blob(), blobInfo.filename());
  
        xhr.send(formData);
      },
    });
  }
}
