Updatecategory(){
   let name=$('#edit_category_name').val();
  let description= $('#edit_category_description').val();

  if(description==''||name==''){
    iziToast.warning({
      message: "Please enter the details",
      position: 'topRight'
  });
  return false
  }
  description= description.replace( /[\r\n]+/gm, " " );


  // var new_array = [];
  // this.editsubcateogry.forEach(element => {
  //   new_array.push(element.name);
  // });
  // var Supcategory = new_array.join(",");


  let api_req:any = '{"operation": "category","moduleType": "category","api_type": "web","element_data": {"action":"Updatecategory","id":"'+this.edit_cat_id+'","category_name":"'+name+'","description":"'+description+'"}}';
  this.serverService.sendserver(api_req).subscribe((response: any)=>{
    console.log(response)
    if(response.result.data==true){
      $('#edit_category').modal('hide');       

      Swal.fire(
        'Updated!',
        'success'
      );
     this.getcategory();
    }else{
      Swal.fire(
        'Oops!',
        'error'
        );
    }
},(error: any)=>{
    console.log(error)
});
 
}

